}

:root {
  --palette-color-light: #ffffff;
  --palette-color-glow: #ffffff55;
  --palette-color-dark: #000;
  --palette-color-dark-overlay: #00000011;
  --palette-color-focus-overlay: #4ea9fd11;
  --palette-color-light-shadow: #00000022;
  --palette-color-shadow: #00000055;
  --palette-color-1-light: #eee;
  --palette-color-1-medium: #ccc;
  --palette-color-1-dark: #666;
  --palette-color-2-light: #4ea9fd;
  --palette-color-2-medium: #0385fd;
  --palette-color-2-dark: #27537d;
  --palette-color-3-light: #F79E99;
  --palette-color-3-medium: #F5584F;
  --palette-color-3-dark: #C2453E;
  --radius-small: 4px;
}
body,
html,
input,
select,
textarea {
  font-family: 'Exo 2', "Ubuntu", "sans-serif";
}

