  font-size: 1.25rem;
  border-radius: var(--radius-small);
font-weight: bold
}
.connector-login img, .connector-login-loading img {
  width: 1rem;
  height: 1rem;
  background-color: var(--palette-color-light);
  border-radius: 50%;
  padding: 0.25rem;
  margin-right: 1rem;
}
.connector-login-loading img { 
  width: 2.5rem;
  height: 2.5rem;
  padding: 0.5rem;
  background-color: var(--palette-color-1-medium);

}
.connector-login span {
