.dropdown-container {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
}
.dropdown {
  border: 1px solid var(--palette-color-1-medium);
  height: 2.5rem;
  border-radius: var(--radius-small);
  background-color: var(--palette-color-glow);
