.dropdown::after {
  content: "";
  position: absolute;
  right: 1rem;
  top: 0.9rem;
  border: 0.7rem solid transparent;
  border-top: 0.7rem solid var(--palette-color-2-medium);
  transition: 0.25s;
}

.dropdown-container.open .dropdown {
  border: 1px solid var(--palette-color-2-medium);
