}
ul.menu {
  margin: 0;
  list-style-type: none;
  padding: 0;
  margin-block: 0;
}
ul.menu li {
  list-style-type: none;
  display: flex;
  margin: 0;
  padding: 0.75rem 1rem;
  border-left: 0.5rem solid var(--palette-color-1-medium);
  
  align-items: center;
  cursor: pointer;
  transition: 0.5s;
}
ul.menu li svg { 
  width:1.75rem;
  aspect-ratio: 1/1;
  margin-right:1rem;
  background-color: var(--palette-color-light);
  padding: 0.25rem;
  border-radius: 1.5rem;
}
ul.menu li.selected {
  background-color: var(--palette-color-1-light);
  border-left: 0.5rem solid var(--palette-color-2-light);
  padding-left: 1rem;
  transition: 0.5s;
}
ul.menu li span {
  flex-grow: 1;
  font-size: 1.25rem;
  font-weight: bold;
  color: var(--palette-color-1-dark);
  transition: 0.5s;
}
ul.menu li.selected span {
  color: var(--palette-color-2-medium);
  transition: 0.5s;
}
ul.menu li div {
  background-color: var(--palette-color-1-dark);
  color: var(--palette-color-light);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 1.5rem;
  width: 1.5rem;
  border-radius: 1rem;
  font-size: 0.75rem;
  font-weight: bold;
  transition: 0.5s;
}
ul.menu li.selected div {
  background-color: var(--palette-color-2-dark);
  transition: 0.5s;
}

.main-structure {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: grid;
  grid-template-rows: 10vh 90vh;
  grid-template-columns: 15vw 85vw;
}
.main-structure header {
  background: var(--palette-color-1-dark) !important;
  grid-column: 1 / 3;
  grid-row: 1;
  display: flex;
  align-items: center;
  padding: 2rem;
  position: relative;
}
.main-structure aside {
  padding-top: 1rem;
  background-color: var(--palette-color-1-medium) !important;
  grid-column: 1 / 2;
  grid-row: 2;
  display: flex;
  flex-direction: column;
}
.main-structure article {
  background-color: var(--palette-color-light) !important;
  grid-column: 2/ 3;
  grid-row: 2;
  padding: 2rem;
  display: flex;
  flex-direction: column;
}
.main-structure header svg {
  height: 2.5rem;
  aspect-ratio: 1/1;
  margin: 0 2rem;
  position: relative;
  -webkit-transform: translateY(-0.75rem);
          transform: translateY(-0.75rem);
}
.main-structure header span {
  position: relative;
  cursor: pointer;
  display: block;
  box-sizing: border-box;
  border: 1px solid transparent;
  padding: 1.25rem 0;
  transition: 0.2s;
}
.main-structure header span:hover {
  box-shadow: inset 3px 3px 5px var(--palette-color-glow),
    0px 0px 5px var(--palette-color-light-shadow);
  border-radius: 50%;
  transition: 0.2s;
}

.main-structure header span:active {
  box-shadow: inset 0px 0px 5px var(--palette-color-light-shadow),
    0px 0px 5px var(--palette-color-glow);
  border-radius: 50%;
  transition: 0.2s;
}
.main-structure header span::after {
  content: attr(aria-alt);
  color: var(--palette-color-light) !important;
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
  bottom: 0.75rem;
  font-size: 1rem;
}
.main-structure header .message {
  position: absolute;
  right: 0;
  bottom: 0;
  background-color: var(--palette-color-1-light);
  padding: 0.5rem;
  font-weight: bold;
  color: var(--palette-color-1-dark);
  border-left: 0.25rem solid var(--palette-color-1-medium);
}

